___CSS_LOADER_EXPORT___.push([module.id, `body,
html {
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    height: 99%;
}

.form-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.form-control {
    position: relative;
    margin-bottom: 10px;
}

.input {
    height: 20px;
}

.button-container {
    margin-top: 10px;
}

.btn {
    margin: auto;
    cursor: pointer;
    background-color: red;
    font-size: 17px;
    font-weight: bold;
    border: 1px solid transparent;
    border-radius: 4px;
    text-align: center;

}

.btn:active {
    box-shadow: inset 0 0 15px rgba(0, 0, 0, .5);
    opacity: 0.2;
}

.btn:hover {
    opacity: 0.7;
}

.form-error {
    border: 1px solid black;
    position: absolute;
    padding: 5px 10px;
    padding-top: 0px;
    width: 200px;
    background: #fff;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1);
    z-index: 999;
}

.form-error-title {
    font-size: 14px;
    font-weight: bold;
    width: 100%;
    margin-left: -10px;
    padding: 5px 10px;
    margin-bottom: 5px;
    background-color: #f7f7f7;
    border-bottom: 1px solid #ebebeb;
}

.form-error-text {
    font-size: 10px;
    color: #212529;
}

.form-error-arrow {
    position: absolute;
    left: -26px;
    border: 10px solid transparent;
    border-right: 16px solid black;
}

